import UIButton from "@app/libs/ui/UIButton";
import UIImage from "@app/libs/ui/UIImage";
import UIList from "@app/libs/ui/UIList";
import UIListItem from "@app/libs/ui/UIListItem";
import { observer } from "mobx-react-lite";
import React from "react";
import { Text, View } from "react-native";

export default observer(
  (data: { kelas: any; list: any[]; loading: boolean; navigation: any }) => {
    if (!data.list) return null;

    return (
      <View style={{ paddingHorizontal: 10 }}>
        <View
          style={{
            borderBottomColor: "#ddd",
            borderBottomWidth: 2,
            flexDirection: "row",
            alignItems: "center",
            padding: 10,
            justifyContent: "space-between"
          }}
        >
          <Text style={{ color: "#999" }}>
            Total Murid: {data.list.length}{" "}
          </Text>
          <View
            style={{
              flexDirection: "row",
              alignItems: "center"
            }}
          >
            <UIButton
              size="small"
              fill="outline"
              style={{ margin: 0, marginLeft: 20 }}
              labelStyle={{
                flexDirection: "row",
                alignItems: "center",
                paddingTop: 2,
                paddingBottom: 2,
                paddingHorizontal: 0
              }}
            >
              <UIImage
                source={require("@app/imgs/share.png")}
                style={{ width: 12, height: 12, marginLeft: 8 }}
              />
              Export CSV
            </UIButton>
            <UIButton
              size="small"
              style={{ margin: 0, marginLeft: 8 }}
              labelStyle={{
                flexDirection: "row",
                alignItems: "center",
                paddingTop: 2,
                paddingBottom: 2,
                paddingHorizontal: 0
              }}
              onPress={() => {
                data.navigation.navigate("MuridDetail", {
                  item: {},
                  kelas: data.kelas
                });
              }}
            >
              <UIImage
                source={require("@app/imgs/plus.png")}
                style={{ width: 12, height: 12, marginLeft: 8 }}
              />
              Tambah Siswa
            </UIButton>
          </View>
        </View>
        <UIList
          data={data.list}
          loading={data.loading}
          render={(list: any) => {
            const item = list.item;
            return (
              <UIListItem
                onPress={() => {
                  data.navigation.navigate("MuridDetail", {
                    item,
                    kelas: data.kelas
                  });
                }}
              >
                <View style={{ flexDirection: "row", alignItems: "center" }}>
                  <Text
                    style={{
                      width: 30,
                      fontSize: 14,
                      fontWeight: "bold",
                      alignItems: "right"
                    }}
                  >
                    {list.index + 1}
                  </Text>
                  <View>
                    <Text style={{ fontSize: 16 }}>{item.nama_murid}</Text>
                    <Text style={{ fontSize: 13 }}>
                      {item.is_active === "y" ? "Aktif" : "Non-Aktif"}
                      &bull;
                    </Text>
                    <Text
                      style={{
                        fontSize: 13,
                        color: item.data ? "#ccc" : "#333"
                      }}
                    >
                      {item.data && ` Data diri tersedia`}
                    </Text>
                  </View>
                </View>
                <View>
                  <Text
                    style={{ fontSize: 12, color: "#999", textAlign: "right" }}
                  >
                    NSA: {item.nsa}
                  </Text>
                  <Text
                    style={{ fontSize: 12, color: "#999", textAlign: "right" }}
                  >
                    NISN: {item.nisn}
                  </Text>
                </View>
              </UIListItem>
            );
          }}
        />
      </View>
    );
  }
);
